button[data-start] {
  letter-spacing: .04em;
  color: #fff;
  min-width: 169px;
  height: 56px;
  cursor: pointer;
  background-color: green;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

button[data-stop] {
  letter-spacing: .04em;
  color: #fff;
  min-width: 169px;
  height: 56px;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.5;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

